.footer-mobile {
    font-weight: 400;
    font-size: 14px;
    line-height: calc(17 / 14 * 100%);
    &__container{
        background-color: #ffffff;
        max-width: 768px;
        margin: 0 auto;
    }
    &__logo {
    }
    &__body {
        margin: 120px 0 20px 0;
        &-icon {
            margin-bottom: 169px;
        }
    }
    &__block {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    &__title {
        color: rgba(31, 32, 65, 0.75);
        margin-bottom: 25px;
    }
    &__icon {
        margin-right: 20px;
        &:last-child {
            margin-right: 0;
        }
    }
}
    